@use '../base/variables' as v;
@use '../base/typography' as t;

@mixin sp_ratios {
  .sp_ratios {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;

    & :first-child {
      align-self: flex-start;
    }

    & :last-child {
      align-self: flex-end;
    }

    &-card {
      @include t.h2-vd-magenta;
      display: flex;
      justify-content: start;
      align-items: center;
      padding: 2rem 3rem;
      border-radius: .8rem;
      background-color: v.$lg-magenta;
      width: 44.5rem;
    }

    &-stars {
      margin-right: 3rem;
    }

    &-star {
      padding-right: 0.5rem;
    }
    
  }

  @media only screen and (max-width: v.$bp-1100) {
    .sp_ratios {
      // justify-self: center;
      // width: 80%;

      &-card {
        width: 42rem;
      }

      &-stars {
        margin-right: 1.5rem;
      }

      & :first-child {
        justify-self: start;
      }
      & :last-child {
        justify-self: end;
      }
    }
  }

  @media only screen and (max-width: v.$bp-500) {
    .sp_ratios {
      width: 33rem;
      justify-self: center;

      & :first-child {
        align-self: center;
      }
  
      & :last-child {
        align-self: center;
      }

      &-card {
        width: 100%;
        display: block;
        text-align: center;
        padding: 1.25rem 2rem;
      }

      &-stars {
        margin-right: 0;
        margin-bottom: .75rem;
      }
    }
  }
}