@use 'variables' as v;

@mixin h1-vd-magenta {
  font-size: 4.6rem;
  font-weight: 700;
  color: v.$vd-magenta;
  line-height: 4.8rem;
}

@mixin h2-vd-magenta {
  font-size: .9rem;
  font-weight: 700;
  color: v.$vd-magenta;
}

@mixin h3-white {
  font-size: 1.35rem;
  font-size: 700;
  color: v.$white;
}

@mixin parragraph($fz, $fw, $c, $lh) {
  font-size: $fz;
  font-weight: $fw;
  color: $c;
  line-height: $lh;
}