@use '../base/variables' as v;
@use '../base/typography' as t;

@mixin sp_intro {
  .sp_intro {
    
    &-tittle {
      @include t.h1-vd-magenta;
      margin-bottom: 1.5rem;
    }

    &-text {
      @include t.parragraph(1.5rem, 400, v.$dg-magenta, 2.5rem);
    }
  }

  @media only screen and (max-width: v.$bp-1100) {
    .sp_intro {
      justify-self: center;
      width: 80%;
      text-align: center;
    }
  }

  @media only screen and (max-width: v.$bp-500) {
    .sp_intro {
      width: 34.5rem;

      &-tittle {
        text-align: center;
        width: 25rem;
        font-size: 3rem;
        line-height: 3rem;
        margin: auto;
        margin-bottom: 2.3rem;
      }
    }
  }
}