@use '../base/variables' as v;
@use '../base/typography' as t;
@use '../layout/article' as a;
@use '../layout/ratio' as r;
@use '../layout/cards' as c;

.sp {
  width: 111rem;
  margin: 22rem auto;

  display: grid;
  row-gap: 7.5rem;
  grid-template-columns: 46rem 1fr;
  grid-template-rows: auto 26.5rem;
  column-gap: 11.5rem;

  @media only screen and (max-width: v.$bp-1100) {
    width: 90%;
    margin: 10rem auto;
    grid-template-columns: 1fr;
    justify-content: center;
    row-gap: 3rem;
  }
  
}

@include a.sp_intro;

@include r.sp_ratios;

@include c.sp_cards;

.github {
  padding: 2rem .5rem;
  display: flex;
  justify-content: center;
  align-items: center;

  & .attribution {
    margin-right: 1rem;
  }

  & .ms_logo {
    width: 2rem;
  }
}