@use 'variables' as v;

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  font-family: 'Spartan', sans-serif;
  background: 
  url('../images/bg-pattern-top-desktop.svg') top left no-repeat,
  url('../images/bg-pattern-bottom-desktop.svg') bottom right no-repeat, 
  v.$white;
}