@use '../base/variables' as v;
@use '../base/typography' as t;

@mixin sp_cards {

  .sp_card {
    grid-column: 1 / -1;

    display: flex;
    justify-content: space-between;
    align-items: center;

    & :first-child{
      align-self: flex-start;
    }

    & :last-child {
      align-self: flex-end;
    }

    &-item {
      width: 35rem;
      padding: 4rem 3.25rem;
      background-color: v.$vd-magenta;
      border-radius: 1rem;
    }

    &-buyer {
      display: flex;
      align-items: center;
      margin-bottom: 2.8rem;
    }

    &-img {
      margin-right: 2rem;
      width: 4rem;
      border-radius: 50%;
    }

    &-name {
      @include t.h3-white;
      margin-bottom: .5rem;
    }

    &-ver {
       @include t.parragraph(1.25rem, 400, v.$s-pink, auto);
    }

    &-txt {
      @include t.parragraph(1.25rem, 500, v.$white, 2rem);
    }
  }

  @media only screen and (max-width: v.$bp-1100) {
    .sp_card {
      justify-self: center;
      flex-direction: column;

      & :first-child{
        align-self: center;
      }
  
      & :last-child {
        align-self: center;
      }

      &-item {
        width: 80%;

        &:not(:last-child) {
          margin-bottom: 2rem;
        }
      }
    }
  }

  @media only screen and (max-width: v.$bp-500) {
    .sp_card {
            
      &-item {
        width: 33rem;
      }
    }
  }
  
}